.film-list {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
}

.film-item {
  width: 394px;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.film-item:hover {
  transform: scale(1.03);
}

.film-card-section {
  padding-bottom: 40px;
  padding-top: 20px;
}

.film-img {
  width: auto;
  height: auto;
  border-radius: 10px;
}

.film-name {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 116%;
}

.film-tag {
  line-height: 133%;
  color: #ff6b08;
}

