 } */

/* tablet */

@media screen and (min-width: 768px) {
  .film-img {
    height: 504px;
    width: auto;
  }
  .film-list {
    gap: 32px;
    justify-content: center;
  }

  .film-item {
    width: 336px;
  }

  .film-name {
    line-height: 133%;
  }
  .film-card-section {
    padding-bottom: 60px;
    padding-top: 60px;
  }
}

/* desctop */

@media screen and (min-width: 1280px) {
  .film-img {
    width: auto;
    height: 504px;  
  }
  .film-list {
    row-gap: 32px;
    column-gap: 16px;
  }
  .film-name {
    font-size: 16px;
    line-height: 120%;
  }
  .film-tag {
    line-height: 120%;
  }
}
